.portfolio-section {
  padding-bottom: var(--offset-section);
  background-color: var(--c-light);

  &__top {
    display: flex;
    align-items: end;
    justify-content: space-between;
    margin-bottom: 48px;
  }

  &__title {
    margin: 0;
    padding-bottom: 10px;
    max-width: 57%;
  }

  &__slider-nav {
    transform: translateX(12px);
  }
  &__slider {
    margin: -150px!important;
    padding: 150px!important;
  }
}
.portfolio-section .swiper-slide {
  opacity: 0;
  transition: opacity .3s ease-in-out;
}
.portfolio-section .swiper-slide-visible {
  opacity: 1;
}
