.social {
  @include flex-v-center;

  &__item {
    &:not(:last-child) {
      margin-right: 16px;
    }
  }

  &__link {
    display: block;
    width: 24px;
    height: 24px;
    color: inherit;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: currentColor;
      object-fit: cover;
    }
  }
}
