.testimonials {
  position: relative;
  z-index: 5;
  padding-top: 58px;
  background-color: var(--c-bg-gray);

  &__content {
    grid-column: 7 span;
    max-width: 612px;
  }

  &__title {
    margin-bottom: 60px;
  }

  &__slider {
    position: relative;
    padding-bottom: 12px;
    padding-right: 12px;
  }

  &__slider-nav {
    position: absolute;
    bottom: 0;
    right: 0;
  }

  &__img {
    grid-column: 8 / 5 span;
    z-index: 5;
    margin-bottom: -60px;
  }
}

.testimonials-item {
  &__img {
    margin-bottom: 24px;
    border-radius: 100%;
    width: 72px;
    height: 72px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    overflow: hidden;

    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__descr {
    margin-top: 0;
    margin-bottom: 24px;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--c-gray);
  }

  &__author {
    display: block;
    margin-bottom: 4px;
    padding-right: 110px;
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: var(--c-title);
  }

  &__author-position {
    display: block;
    padding-right: 110px;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--c-descr);
  }

}
