  // base
  :root {
    // base
    --font-family: "Ubuntu", sans-serif;
    --second-family: "Lato", sans-serif;
    --content-width: 1230px;
    --container-offset: 15px;
    --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

    // colors
    --c-light: #fff;
    --c-text-footer: rgb(255, 255, 255, 0.6);
    --c-gray: #424551;// --gray-800
    --c-title: #1e212c;// --gray-900
    --c-descr: #787a80;// --gray-700
    --c-primary: #ff5a30;
    --c-active: #fc3300;
    --c-bg-gray: #f4f5f6;
    // --c-manatee-gray: #9a9ca5;
    // --c-bombay-gray: #b3b7bc;
    // --c-iron-gray: #d7dadd;
    // --c-athens-gray: #e5e8ed; --gray-300
    // --c-alabaster-gray: #f9f9f9;

    // layout
    --offset-section: 120px;
    --offset-middle: 80px;
    --offset-col: 30px;
    //--offset-mb:24px
  }
