.video-block {
  position: relative;
  overflow: hidden;
  border-radius: 4px;
  max-height: 500px;

  &::before {
    content: '';
    @include absolute-full;
    z-index: 3;
    background-color: rgba(#1e212c, 0.6);

  }

  &__play {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 10;
    border-radius: 100%;
    width: 80px;
    height: 80px;
    color: var(--c-light);
    background-color: var(--c-primary);
    transform: translate(-50%, -50%);

    svg {
      position: absolute;
      top: 50%;
      left: calc(50% + 3px);
      width: 26px;
      height: 26px;
      fill: currentColor;
      transform: translate(-50%, -50%);
    }
  }
  &__mited {
    position: absolute;
    left: 32px;
    bottom: 32px;
    z-index: 10;
    border-radius: 100%;
    width: 40px;
    height: 40px;
    color: var(--c-light);
    background-color: rgba(#fff, 0.12);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

    svg {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 20px;
      height: 20px;
      fill: currentColor;
      transform: translate(-50%, -50%);
    }
  }

  &__content {
    display: block;
    border-radius: 4px;
    width: 100%;
    height: 100%;


  }
}
