.header-contacts {
  @include flex-v-center;

  &__link {
    &:not(:last-child) {
      margin-right: 36px;
    }
  }
}

.contacts-link {
  @include flex-column-start;

  padding-left: 52px;
  background-position: left center;
  background-repeat: no-repeat;
  background-size: 40px 40px;

  &--phone {
    background-image: url(../img/phone.svg);
  }

  &--mail {
    background-image: url(../img/chat.svg);
  }

  &__caption {
    font-weight: 700;
    font-size: 14px;
    line-height: 150%;
    color: var(--c-gray);
  }

  &__value {
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: var(--c-gray);
  }
}
