.portfolio-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  border-radius: 4px;
  box-shadow:
  0 4px 4px -4px rgba(30, 33, 44, 0.05),
  0 12px 10px -6px rgba(154, 156, 165, 0.08),
  0 30px 24px -10px rgba(154, 156, 165, 0.1),
  0 80px 80px -20px rgba(154, 156, 165, 0.16);
  background: var(--c-light);
  &__img {
    border-radius: 4px 4px 0 0;
  }
  &__content {
    @include flex-column-all-center;
    padding: 16px 20px 24px;
  }
  &__title {
    margin-bottom: 4px;
  }
  &__descr {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    text-align: center;
    color: var(--c-descr);
  }
  &__btn {
    display: none;
    margin-top: 24px;
  }
}
