.news-section {
  padding-top: 60px;
  padding-bottom: var(--offset-section);
  background-color: var(--c-light);

  &__title {
    margin-bottom: 60px;
  }

  &__list {
    grid-template-rows: repeat(2, 255px);
    grid-auto-rows: 255px;
    // row-gap: var(--offset-col);
  }

  &__item:first-child {
    grid-column: 7 span;
    grid-row: 2 span;

    .news-article__content {
      padding-top: 24px;
    }
  }
  &__item:not(:first-child) {
    grid-column: 5 span;
    grid-row: 1 span;

    p {
      display: none;
    }

    // img {
    //   max-height: 156px;
    // }
  }
}
