.contacts-section {
  @include bg-img(contacts-section-bg);
  background-color: var(--c-gray);

  &__container {
    padding-top: var(--offset-middle);
    padding-bottom: var(--offset-middle);
  }

  &__form {
    @include flex-column-center;
    grid-column: 8 / 5 span;
  }

  &__btn {
    align-self: center;
  }
}
