.footer {
  padding: var(--offset-middle) 0;
  color: var(--c-text-footer);
  background-color: var(--c-title);

  &__container {
    row-gap: 48px;
  }

  &__left {
    grid-column: 5 span;
  }

  &__top {
    display: flex;
    align-items: start;
  }

  &__from {
    grid-column: 8 / 6 span;
    padding-left: 18px;
  }

  &__logo {
    margin-right: 60px;
  }

  &__descr {
    margin: 0;
    margin-top: 22px;
    max-width: 495px;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: inherit;
  }

  &__address {
    grid-column: 4 span;
  }

  &__nav {
    grid-column: 6 span;
    padding-left: 65px;
  }

  &__copyright {
    grid-column: 12 span;
    padding-top: 15px;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    color: var(--c-light);

    span {
      padding-left: 20px;
      background-image: url("../img/footer-love.svg");
      background-position: center left;
      background-size: 16px 16px;
      background-repeat: no-repeat;
    }
  }
}

.footer-from {

  &__title {
    margin-top: 0;
    margin-bottom: 24px;
    font-weight: 700;
    font-size: 24px;
    line-height: 150%;
    color: var(--c-light);
  }

  &__item {
    @include flex-v-center;
    margin-bottom: 16px;
    width: 100%;
  }
  &__field {
    width: 100%;
  }
  &__input {
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    background-color: rgba(255, 255, 255, 0.12);
  }

  &__btn {
    padding: 11px 24px 12px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }

  &__decsr {
    margin: 0;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    color: inherit;
  }
}
.footer-address {

  &__item {
    display: block;

    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }

  &__name,
  &__value  {
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
  }

  &__name {
    color: var(--c-light);
  }

  &__value {
    color: inherit;
  }
}
.footer-title {
  margin-top: 0;
  margin-bottom: 13px;
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: var(--c-light);
}
.footer-nav {
  display: flex;
  align-items: start;
  gap: 124px;

  &__item {
    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: inherit;
  }
}
