.hero {
  min-height: 988px;
  background-color: rgba(#1e212c, 0.6);
  background-image: url(../img/hero/bg-hero.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;

  &__content {
    padding-top: 220px;
    max-width: 595px;
  }

  &__btns {
    .btn:not(:last-child) {
      margin-right: 24px;
    }
  }
}
