.mission {
  --offset-mission: 94px;
  background-color: var(--c-light);

  &__list {
    grid-template-columns: repeat(3, 1fr);
    column-gap: calc(var(--offset-mission)*2);
    margin-bottom: 31px;
  }
  &__item {
    position: relative;
    padding-top: 72px;
    text-align: center;
    background-position: top center;
    background-repeat: no-repeat;
    background-size: 48px 48px;

    &:not(:last-child) {

      &::after {
        content: '';
        position: absolute;
        top: 0;
        right: calc(-1 * var(--offset-mission));
        width: 1px;
        height: 100%;
        background-position: center;
        background-image: url(../img/mission/divider.svg);
        background-repeat: no-repeat;
        background-size: cover;
      }
    }
    &--quality {
      background-image: url(../img/mission/quality.svg);
    }
    &--safety {
      background-image: url(../img/mission/safety.svg);
    }
    &--comfort {
      background-image: url(../img/mission/comfort.svg);
    }
  }

  &__subtitle {
    margin-bottom: 8px;
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--c-descr);
  }
}
