.slider-nav {
  position: relative;
  @include flex-v-center;
  justify-content: end;
  z-index: 15;
  user-select: none;
}
.slider-btn {
  @include flex-all-center;
  border-radius: 100%;
  width: 48px;
  height: 48px;
  color: var(--c-gray);
  background-color: transparent;
  transition: background-color .3s ease-in-out, color .3s ease-in-out;

  &__icon {
    width: 24px;
    height: 24px;
    color: inherit;
    fill: currentColor;
    object-fit: cover;
  }
  @include hover {
    color: var(--c-light);
    background-color: var(--c-primary);
  }
}
