.form {
  border: 1px solid #e5e8ed;
  padding: 47px 40px;//повтор top
  border-radius: 4px;//повтор
  box-shadow:
    0 4px 4px -4px rgba(30, 33, 44, 0.05),
    0 12px 10px -6px rgba(30, 33, 44, 0.08),
    0 26px 24px -10px rgba(30, 33, 44, 0.1),
    0 60px 80px -20px rgba(30, 33, 44, 0.16);
  background-color: var(--c-light);//повтор

  &__title {
    margin-bottom: 24px;
  }

  &__item {
    display: block;

    &:not(:last-child) {
      margin-bottom: 19px;
    }
  }

}
.form-field {

  &__caption {
    display: block;
    margin-bottom: 8px;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--c-gray);
  }

  &__input {
    padding: 11px 15px;
    border: 1px solid #d7dadd;
    border-radius: 4px;
    min-width: 260px;
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    background-color: #f4f5f6;

    &::placeholder {
      color: #9a9ca5;
    }
    &--big {
      resize: none;
    }
  }
  &--big {
    flex-grow: 1;
  }
}

.custom-checkbox {
  // @include custom-checkbox;

  &__input {
    position: absolute;
    opacity: 0;
  }

  &__text {
    position: relative;
    display: inline-block;
    padding-left: 28px;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--c-gray);

    &::before {
      content: "";
      position: absolute;
      left: -1px;
      top: 1px;
      border: 1px solid var(--c-primary);
      border-radius: 3px;
      width: 16px;
      height: 16px;
      box-sizing: border-box;
      transition: background-color 0.3s ease-in-out;
    }

    &::after {
      content: "";
      position: absolute;
      left: -1px;
      top: 1px;
      width: 16px;
      height: 16px;
      box-sizing: border-box;
      background-image: url("../img/check.svg");
      background-position: center;
      background-size: 10px 8px;
      // background-size: cover;
      background-repeat: no-repeat;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }
  }
  &__input:checked + &__text::before {
    background-color: var(--c-primary);
  }
  &__input:checked + &__text::after {
    opacity: 1;
  }

  // &__input:focus + &__text::before {
  //   outline: 2px solid #f00;
  //   outline-offset: 2px;
  // }
}
