.know-form {
  position: relative;
  top: 92px;
  padding: 47px 60px;
  box-shadow:
    0 4px 4px -4px rgba(30, 33, 44, 0.03),
    0 12px 10px -6px rgba(154, 156, 165, 0.04),
    0 30px 24px -10px rgba(154, 156, 165, 0.05),
    0 80px 80px -20px rgba(154, 156, 165, 0.08);

  &__title {
    margin-bottom: 32px;
  }

  &__content {
    @include flex;
    align-items: flex-end;
    width: 100%;
  }

  &__field {
    margin-right: 24px;
  }
}
