.nav {
  margin-left: -112px;

  &__list {
    @include flex-v-center;
}

  &__item {
    &:not(:last-child) {
      margin-right: 40px;
    }
  }

  &__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: var(--c-gray);
    transition: color .3s ease-in-out;

    @include hover {
      color: var(--c-primary);
    }

    &:active {
      color: var(--c-active);
    }
  }
}
