html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family);
  font-weight: 400;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  // height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: var(--c-light);

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: var(--offset-col);
}

.main-title {
  margin: 0;
  margin-bottom: 24px;
  font-weight: 700;
  font-size: 72px;
  line-height: 130%;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: var(--c-title);

  &--home {
    color: var(--c-light);

    span {
      color: var(--c-primary);
    }
  }
}
.main-descr {
  margin: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 150%;
  color: var(--c-descr);

  &--home {
    margin-bottom: 61px;
    color: var(--c-light);
  }
}

.btn {
  padding: 13px 31px;
  border-style: solid;
  border-width: 1px;
  border-radius: 4px;
  border-color: transparent;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.375;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  text-align: center;
  color: var(--c-light);

  &:focus {
    outline: none;
  }
  &:disabled {
    opacity: 0.5;
    cursor: none;
  }
  &--big {
    padding: 15px 38px;
    font-size: 16px;
    letter-spacing: 0.03em;
  }
  &--small {
    padding: 15px 24px;
    font-size: 12px;
  }
  &--fill {
    background-color: var(--c-primary);
    transition: background-color .3s ease-in-out, color .3s ease-in-out;

    @include hover {
      background-color: var(--c-active);
    }

  }
  &--stroke {
    border-color: var(--c-primary);
    color: var(--c-primary);
    transition: background-color .3s ease-in-out, color .3s ease-in-out;

    @include hover {
      background-color: var(--c-primary);
      color: var(--c-light);
    }

    &-light {
      border-color: var(--c-light);
      color: var(--c-light);
      transition: background-color .3s ease-in-out, color .3s ease-in-out;

      @include hover {
        background-color: var(--c-light);
        color: var(--c-gray);
      }
    }
  }
}
