.title {
  margin: 0;
  margin-bottom: 24px;
  font-weight: 700;
  font-size: 46px;
  line-height: 130%;
  color: var(--c-title);

  &--middle {
    margin: 0;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    color: var(--c-title);
  }
  &--item {
    margin: 0;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--c-title);
  }
}

.descr {
  margin: 0;
  margin-bottom: 60px;
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
  color: var(--c-descr);
}

.section-offset {
  padding-top: var(--offset-section);
}
