.news-article {
  height: 100%;

  &__img {
    display: block;
    overflow: hidden;
    border-radius: 4px 4px 0 0;
  }

  &__content {
    padding: 16px 24px 24px;
    border-radius: 0 0 4px 4px;
    box-shadow: 0 4px 4px -4px rgba(30, 33, 44, 0.03), 0 12px 10px -6px rgba(154, 156, 165, 0.04), 0 30px 24px -10px rgba(154, 156, 165, 0.05), 0 80px 80px -20px rgba(154, 156, 165, 0.08);
    background-color: var(--c-light);
  }

  &__title {
    margin-top: 0;
    margin-bottom: 12px;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--c-title);
  }

  &__link {
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--c-title);
  }

  &__excerpt {
    margin: 0;
    margin-top: 16px;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--c-gray);
  }
}
.artical-meta {


  &__item {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--c-descr);

    &:not(:first-child) {
      padding-left: 7px;
    }

    &:not(:last-child) {
      padding-right: 12px;
      border-right: 1px solid var(--c-descr);
    }

    &:last-child {
      padding-left: 32px;
    }

    &--comments {
      background-image: url("../img/news/comments.svg");
      background-repeat: no-repeat;
      background-position: center left 8px;
      background-size: 16px 16px;
    }

  }

}
