.services-section {
  padding-top: 212px;
  padding-bottom: var(--offset-middle);
  background-image: url(../img/services-section/bg-services-section.png);
  background-color: var(--c-bg-gray);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;

  &__item {
    grid-column: 3 span;
  }
  &__link {
    position: relative;
    display: block;
    padding: 87px 40px;
    border: 1px solid var(--c-bg-gray);
    border-radius: 4px;
    box-shadow:
      0 4px 4px -4px rgba(30, 33, 44, 0.03),
      0 12px 10px -6px rgba(154, 156, 165, 0.04),
      0 30px 24px -10px rgba(154, 156, 165, 0.05),
      0 80px 80px -20px rgba(154, 156, 165, 0.08);
    background-color: var(--c-light);

    &::before {
      content: '';
      position: absolute;
      inset: 0;
      border-radius: inherit;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      object-fit: cover;
      z-index: 3;
      opacity: 0;
      transition: opacity .3s ease-in-out;

      .services-section__item:nth-child(1) & {
        background-image: url(../img/services-section/construction.jpg);
      }
      .services-section__item:nth-child(2) & {
        background-image: url(../img/services-section/project.jpg);
      }
      .services-section__item:nth-child(3) & {
        background-image: url(../img/services-section/desing.jpg);
      }
      .services-section__item:nth-child(4) & {
        background-image: url(../img/services-section/repairs.jpg);
      }
    }
    &::after {
      content: '';
      position: absolute;
      inset: 0;
      border-radius: inherit;
      background-color: rgba(#1e212c, 0.6);
      z-index: 5;
      opacity: 0;
      transition: opacity .3s ease-in-out;
    }
    @include hover {
      &::before {
        opacity: 1;
      }
      &::after {
        opacity: 1;
      }
      .services-section__icon {
        fill: var(--c-light);
      }
      .services-section__title {
        color: var(--c-light);
      }
    }
  }
  &__content {
    position: relative;
    z-index: 10;
    @include flex-column-all-center;
  }
  &__icon {
    margin-bottom: 24px;
    width: 52px;
    height: 52px;
    fill: var(--c-primary);
    transition: fill .3s ease-in-out;
  }

  &__title {
    transition: color .3s ease-in-out;
  }
}

