.facts {
  position: relative;
  padding-top: 180px;
  padding-bottom: var(--offset-section);
  background-color: var(--c-light);

  &::before {
    content: '';
    position: absolute;
    top: -195px;
    left: -9px;
    width: 570px;
    height: 1065px;
    background-image: url("../img/facts-bg.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &__title {
    margin-bottom: 60px;
  }

  &__list {
    @include flex-all-center;
    margin: 0 auto;
    max-width: 736px;
  }

  &__item:not(:last-child) {
    margin-right: 48px;
  }
}

.facts-element {

  &__circle {
    --progress-stroke: #000;
    position: relative;
    margin-bottom: 20px;
    width: 149px;
    height: 148px;

    svg {
      position: absolute;
      inset: 0;
    }

    &--one-color {
      --progress-stroke: #f89828;
    }
    &--two-color {
      --progress-stroke: #f52f6e;
    }
    &--three-color {
      --progress-stroke: #5a87fc;
    }
    &--four-color {
      --progress-stroke: #03cea4;
    }

    .progress {
      stroke: var(--progress-stroke);
      transform: rotate(-90deg) translateX(24px);
      transform-origin: center;
    }

    .progress-bg {
      stroke: var(--progress-stroke);
      opacity: 0.3;
    }
  }

  &__value {
    position: absolute;
    top: 50%;
    left: 50%;
    font-weight: 700;
    font-size: 32px;
    line-height: 150%;
    text-align: center;
    color: var(--c-title);
    transform: translate(-50%, -50%);
  }

  &__text {
    display: block;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    text-align: center;
    color: var(--c-title);
  }
}
